html {
  scroll-snap-type: y mandatory;
}
.app {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: black;
}
.app__top > h1 {
  text-align: center;
  color: white;
}
.app__logo {
  height: 12vh;
}
.app__videos {
  margin-top: -80px;
  position: relative;
  height: 64vh; /* 16:9 ratio */
  width: 36vh; /* 16:9 ratio */
  background-color: white;
  border-radius: 20px;
  max-width: 450px;
  max-height: 1200px;
  overflow: scroll;
  scroll-snap-type: y mandatory;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.app__videos::-webkit-scrollbar {
  display: none;
}
/* Hide scrollbar for IE, Edge and Firefox */
.app__videos {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}